<template>
  <div>
    <div v-show="display" class="container-card">
      <!-- 搜索筛选区 -->
      <el-row class="serach-box">
        <el-col v-for="item in headerItems" :key="item" :span="4">
          <el-col :span="7" >{{item.label}}</el-col>
          <el-col :span="16"><el-input
            v-model="userQuery[item.prop]"
            clearable
            placeholder="请输入内容"
          >
            <el-button
              @click="query"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
          </el-col>
        </el-col>
         <el-col :span="4">
          <el-date-picker
            v-model="dataValue"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <!-- 按钮区 -->
      <el-row class="serach-box">
        <el-col :span="4"
          >
          <el-col :span="7">状态</el-col>
          <el-col :span="16"><el-select
            
            v-model="userQuery.samplePlanState"
            placeholder="请选择模板"
            @change="query"
          >
            <el-option
              v-for="item in statusTypeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">批量修改</el-col>
          <el-col :span="16"><el-select v-model="value" placeholder="请选择模板">
            <el-option
              v-for="item in statusChangeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select></el-col>
        </el-col>
        <el-col class="btn-area" :span="8" >
          <el-col :span="6">
            <el-button @click="toSample" type="info">更新状态</el-button>
          </el-col>
            <el-col :span="6">
          <el-button type="info" @click="specificationDialogDisplay = true"
            >导出</el-button
          >
        </el-col>
          <el-col :span="6"><el-button type="info" @click="newSingle">新增</el-button></el-col>
          <el-col :span="6"><el-button type="danger" @click="removePreview"
            >删除</el-button
          ></el-col>
        </el-col>
      </el-row>
      <!-- 总览区 -->
      <el-row class="serach-box">
        <el-col class="status-area" :span="12">
          <span>已完成:{{finished}}</span>
          <span>未完成:{{notFinished}}</span>
          <span>修改中:{{modified}}</span>
          <span>取消:{{cancel}}</span>
        </el-col>
      </el-row>
      <!-- 表单区 -->
      <el-table @select="rowclick" :data="tableData" border style="width: 100%">
        <el-table-column type="selection" fixed width="55"> </el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column label="照片" width="200">
          <template slot-scope="scope">
            <el-image
              style="width: 60px; height: 60px; line-height: 60px"
              :src="scope.row.url"
              :preview-src-list="[scope.row.url]"
            >
            </el-image>
            <!-- <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="" />
            </el-dialog> -->
          </template>
        </el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList2"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
            <el-button
              :disabled="!scope.row.operation"
              @click="handleClick(scope.$index)"
              type="text"
              size="small"
              >预览</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 删除对话框 -->
      <el-dialog
        class="summary"
        width="35%"
        title="删除确认"
        :visible.sync="deleteDialogDisplay"
      >
        <el-row>将从系统中删除以下样品信息：</el-row>
        <div class="infor">
          <div v-for="item in selectData" :key="item.index">
            {{ item.sampleNubmber }}
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="deleteDialogDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary" @click="remove">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 汇总单 -->
      <el-dialog
        class="summary"
        width="35%"
        title="汇总单"
        :visible.sync="summaryDialogDisplay"
      >
        <el-row>将根据以下样品信息生成汇总单：</el-row>
        <div class="infor"></div>
        <el-row
          ><el-checkbox v-model="summaryInit"
            >记录并追踪此次汇总结果</el-checkbox
          ></el-row
        >
        <el-row>
          <el-col :span="10">汇总单别名:</el-col>
          <el-col :span="14"
            ><el-input v-model="value" :disabled="!summaryInit"></el-input
          ></el-col>
        </el-row>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="summaryDialogDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 规格书 -->
      <el-dialog
        width="35%"
        title="规格书"
        :visible.sync="specificationDialogDisplay"
        class="summary"
      >
        <el-row
          >将根据以下样品信息生成样品规格书（建议一次不要勾选超过20份样品规格书）：</el-row
        >
        <div class="infor"></div>
        <el-row>
          <el-col :span="10">样品规格书别名:</el-col>
          <el-col :span="14"><el-input v-model="value"></el-input></el-col>
        </el-row>
        <span slot="footer" class="dialog-footer">
          <el-button type="info" @click="specificationDialogDisplay = false"
            >取 消</el-button
          >
          <el-button type="primary">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  GetAllSamplePlansOverview,
  DeleteSamplePlans,
} from "network/proofSingle.js";
import { GetCommonFormState } from "network/common.js";

export default {
  data() {
    return {
      dataValue: null,
      display: true,
      totalAmount: null,
      finished: null,
      notFinished: null,
      modified: null,
      cancel: null,
      url:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
      srcList: [
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        /*  'https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg' */
      ],
      tableData: [
        {
          number: "1",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "2",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: false,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "2",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "1",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
        {
          number: "1",
          samplenumber: "DC20-Q3",
          salesman: "路人甲",
          client: "路人乙",
          typename: "花",
          picture: "123.kb",
          typemachine: "K140",
          needle: "140",
          material: "20*15(平铺) 20*15(平铺)",
          data: "2020.11.18",
          status: "已完成",
          operation: true,
          url:
            "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        },
      ],
      tableList: [
        { label: "表单名称", prop: "alias", width: "100", isFix: "left" },
        { label: "样品号", prop: "sampleNubmber", width: "100", isFix: "left" },
        { label: "业务员", prop: "salesMan" },
        { label: "客户", prop: "customer" },
        { label: "品名", prop: "machineType" },
      ],
      tableList2: [
        { label: "要求机种", prop: "machineType", width: "100" },
        { label: "要求针数", prop: "needl5rfeType", width: "100" },
        {
          label: "要求用料(表纱(表纱混率)+里纱)",
          prop: "materialsRequired",
          width: "150",
        },
        { label: "要求尺寸", prop: "requiredZize" },
        { label: "创建日期", prop: "creationTime", width: "150" },
        { label: "状态", prop: "samplePlanState" },
      ],
      headerItems:[{
        label:"样品号",
        prop:"sampleNubmber",
      },{
        label:"表单名称",
        prop:"alias",
      },{
        label:"业务员",
        prop:"salesMan",
      },{
        label:"客户",
        prop:"customer",
      },{
        label:"针型",
        prop:"needleCount",
      }],
      summaryDialogDisplay: false,
      summaryInit: true,
      specificationDialogDisplay: false,
      deleteDialogDisplay: false,
      selectData: [],
      dialogVisible: false,
      dialogImageUrl: "",
      value: "",
      statusTypeList: [],
      statusChangeList: [],
      object1: {},
      userQuery: {
        alias: "",
        sampleNubmber: "",
        salesMan: "",
        customer: "",
        needleCount: "",
        samplePlanType: 0,
        samplePlanState: 0,
        beginTime: "2021-01-08T02:29:45.612Z",
        endTime: "2021-01-28T02:29:45.612Z",
        pageSize: 10,
        pageNumber: 0,
      },
    };
  },
  created() {
    this.inital();
  },
  watch: {
    dataValue(val) {
      this.userQuery.beginTime = val[0];
      this.userQuery.endTime = val[1];
      this.query();
    },
  },
  methods: {
    //初始化
    inital() {
      this.getProofSingle(this.userQuery);
      this.getStatusList();
    },
    //获得表状态列表
    getStatusList() {
      GetCommonFormState().then((res) => {
        if (res.success) {
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      });
    },
    //获得打样单单个列表
    getProofSingle(data) {
      GetAllSamplePlansOverview(data).then((res) => {
        if (res.success) {
          this.tableData = res.result.samplePlansOverview.items;
          this.totalAmount = res.result.totalAmount;
          this.finished = res.result.finished;
          this.notFinished = res.result.notFinished;
          this.modified = res.result.modified;
          this.cancel = res.result.cancel;
          this.$message.success("获得订打样单列表成功");
        } else {
          this.$message.error("获得打样单列表失败");
        }
      });
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //点击查询
    query() {
      this.getProofSingle(this.userQuery);
    },
    //新增
    newSingle() {
      this.$router.push("/proposal");
    },
    //编辑
    edit(scope){
       this.$router.push({
        path:"/proposal",
        query:{
          iid:scope.row.id
        }
      })
    },
    //删除预览
    removePreview(){
      if(this.selectData.length!=0){
        this.deleteDialogDisplay = true;
      }
      else{
        this.$message.error("请先选择要删除的项！")
      }
      
    },
    //批量删除
    remove() {
      var data = this.selectData;
      if (data.length == 0) {
        this.$message.error("请先选择项！");
      } else {
        let arr = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.id;
        });
        DeleteSamplePlans(arr).then((res) => {
          if (res.success) {
            this.$message.success("删除成功");
          }
        });
      }
    },
    handleClick(scope) {
      console.log(scope.row.client);
    },
    toSample() {
      this.$router.push("/sample");
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      console.log(file);
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    load() {
      this.tableData = this.tableData.concat([{}]);
    },
  },
};
</script>

<style scoped>
.serach-box {
  text-align: left;
  line-height: 40px;
  margin-bottom: 10px;
  font-size: 13px;
}
.summary .el-row {
  text-align: left;
  line-height: 40px;
  margin: 20px 0;
}
.infor {
  height: 40px;
  margin: 10px 0;
}
.status-area span{
  margin-right: 20px;
}
.el-row {
  margin-bottom: 10px;
}
.btn-area .el-button{
  width:100px;
}
</style>