import {request} from './request'

//获得打样单列表
export async function GetAllSamplePlansOverview(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllSamplePlansOverview',
        data:data
    })
}

//新增打样单
export async function AddSamplePlans(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/AddSamplePlans',
        data:data
    })
}

//更新打样单
export async function UpdateSamplePlans(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateSamplePlans',
        data:data
    })
}

//删除打样单
export async function DeleteSamplePlans(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteSamplePlans',
        data:data
    })
}

//获取单个打样单
export async function GetSingleSamplePlan(data){
    return await request({
        url:'/api/BusinessUnit/GetSingleSamplePlan/Single',
        params:{input:data}
    })
}